.Links{
text-align: center;
padding-top: 3%;
padding-bottom: 5%;
}
.Col{
display: inline;
text-decoration: none;
}
.Col a {
text-decoration: none !important;
color: #FFF;
letter-spacing: 1.2px;
font-weight: bold;
font-size: 2rem;
font-family: sans-serif;
margin: 0 2% 0 2%;
font-family: 'Tajawal', sans-serif!important;
}
.Col .Active{
color: #f78916!important;
}
.Col a:hover{
color: #f78916;
}
.Links{
z-index: 9999999;
position: relative;
}
/*start media*/
@media(min-width:320px) and (max-width:450px){
.Links{
padding-top: 13%;
font-family: 'Tajawal', sans-serif!important;
}
.Col a{
font-size: 1.2rem;
font-family: 'Tajawal', sans-serif!important;
}
.Container h1{
font-size: 6rem;
}
}
/*start media tablate*/
@media (min-width:450px) and (max-width:650px){
.Col a{
font-size: 1.8rem;
font-family: 'Tajawal', sans-serif!important;
}
}